* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}
/* Css Variable */
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;500;600&display=swap');
:root {
  --back-color: rgb(3, 43, 3);
  --variant: rgb(53, 180, 53);
  --primary: lightgreen;
  --va-primary: green;
  --light: rgba(255, 255, 255, 0.6);
  --cream: #fff;
  --transition: all 400ms ease;
  --lg-container: 75%;
  --md-container: 86%;
  --ms-container: 90%;
}
/* Scroll */
html {
  scroll-behavior: smooth;
}
::-webkit-scrollbar {
  display: none;
}
body {
  font-family: 'Roboto Mono', monospace;
  background-color: var(--back-color);
  color: var(--cream);
  line-height: 1.7;
}
/* Body Styling */
.container {
  width: var(--lg-container);
  margin: 0 auto;
}
h1, h2, h3, h4, h5 {
  font-weight: 500;
}
h1 {
  font-size: 2.5rem;
}
section {
  margin-top: 8rem;
}
section > h2, section > h5 {
  text-align: center;
  color: var(--light);
}
section > h2 {
  color: var(--primary);
  margin-bottom: 3rem;
}
.text-light {
  color: var(--light);
}
a {
  color: var(--primary);
  transition: var(--transition);
}
a:hover {
  color: var(--cream);
}
.btn {
  width: max-content;
  display: inline-block;
  color: var(--primary);
  padding: 0.75rem 1.2rem;
  border: 1px solid var(--primary);
  border-radius: 0.4rem;
  cursor: pointer;
  transition: var(--transition);
}
.btn:hover {
  background: var(--cream);
  color: var(--back-color);
  border-color: transparent;
}
.btn-primary {
  background: var(--primary);
  color: var(--back-color);
}
img {
  display: block;
  width: 100%;
  object-fit: cover;
}
/* Start For Header */
header {
  color: rgb(198, 236, 140);
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}
.header-container {
  text-align: center;
  height: 100%;
  position: relative;
}
/* The CTA */
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 2rem;
  justify-content: center;
}
/* Media */
.social-media {
  display: flex;
  flex-direction: column;
  gap: .8rem;
  align-items: center;
  position: absolute;
  left: 0;
  bottom: 3rem;
}
.social-media::after {
  content: '';
  width: 1px;
  height: 2rem;
  background: var(--primary);
}
/* Image Header */
.me {
  background: linear-gradient(var(--primary), transparent);
  width: 62rem;
  height: 30rem;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 4rem .5rem 1.5rem 1.5rem;
}
/* Scroll Down */
.scroll-down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: .9rem;
}
/* Start For Navbar */
nav {
  background: rgba(0, 0, 0, 0.3);
  width: max-content;
  padding: .7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  gap: .8rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
}
nav a {
  background: transparent;
  padding: 1rem;
  display: flex;
  border-radius: 50%;
  color: var(--light); /* other colors also good */
  font-size: 1.2rem;
}
nav a:hover {
  background: rgba(0, 0, 0, 0.3);
}
nav a:active {
  background: var(--back-color);
  color: var(--cream);
}
/* About */
.about-container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}
.about-me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(45deg, transparent, var(--primary), transparent);
  display: grid;
  place-items: center;
}
.about-me img {
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  transition: var(--transition);
}
.about-me img:hover {
  transform: rotate(0);
}
.about-content p {
  margin: 2rem 0 2.6rem;
  color: var(--light);
}
.about-header {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}
.about-card {
  background: var(--variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
}
.about-card:hover {
  background: transparent;
  border-color: var(--va-primary);
  cursor: default;
}
.about-icon {
  color: greenyellow;
  font-size: 1.4rem;
  margin-bottom: 1rem;
}
.about-card h5 {
  font-size: .95rem;
}
.about-card small {
  font-size: .7rem;
  color: greenyellow;
}
/* Experience */
.experience-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}
.experience-container > div {
  background: var(--variant);
  padding: 2.4rem 5rem;
  border: 1px solid transparent;
  border-top-right-radius: 2rem;
  border-bottom-left-radius: 2rem;
  transition: var(--transition);
}
.experience-container > div:hover {
  background: transparent;
  border-color: var(--va-primary);
  cursor: default;
}
.experience-container > div h3 {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--primary);
}
.experience-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 2rem;
}
.experience-content > article {
  display: flex;
  gap: 1rem;
}
.experience-icon {
  margin-top: 6px;
  color: var(--primary);
}
/* Portfolio */
.portfolio-item {
  background: rgb(76, 100, 76);
  padding: 2.2rem;
  border: 1px solid transparent;
  border-radius: 1.5rem;
  transition: var(--transition);
}
.portfolio-item:hover {
  border-color: var(--va-primary);
  background: transparent;
}
.portfolio-img {
  border-radius: 1.5rem;
  overflow: hidden;
}
.portfolio-item h3 {
  margin: 1.2rem 0 2rem;
}
.portfolio-cta {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
}
/* Contact */
.contact-container {
  width: 58%;
  display: grid;
  grid-template-columns: 30% 58%;
  gap: 12%;
}
.contact-option {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.option {
  background: var(--variant);
  padding: 1.2rem;
  border-radius: 1.2rem;
  text-align: center;
  border: 1px solid transparent;
  transition: var(--transition);
}
.option:hover {
  background: transparent;
  border-color: var(--va-primary);
}
.option a {
  display: inline-block;
  margin-top: .7rem;
  font-size: .8rem;
}
.contact-icon {
  font-size: 1.5rem;
  margin-bottom: .5rem;
}
/* Form */
form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
input, textarea {
  width: 100%;
  padding: 1.5rem;
  border-radius: .5rem;
  background: transparent;
  border: 2px solid var(--va-primary);
  resize: none;
  color: var(--cream);
}
footer {
  background: var(--primary);
  padding: 2rem 0;
  text-align: center;
  font-size: .9rem;
  margin-top: 7rem;
}
footer a {
  color: rgb(8, 87, 8);
}
footer ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 3rem;
}
.footer-media {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 2rem;
}
.footer-media a {
  display: flex;
  background: var(--back-color);
  color: var(--cream);
  padding: .8rem;
  border-radius: .7rem;
  border: 1px solid transparent;
}
.footer-media a:hover {
  background: transparent;
  color: var(--back-color);
  border-color: var(--back-color);
}
/* Swiper */
.portfolio-container {
  display: flex;
  gap: 3rem;
  margin-top: 5rem;
}
.swiper-container {
  padding-top: 8rem;
  max-width: 100%;
}
.swiper-button-prev,
.swiper-button-next {
  position: absolute;
  height: 50px;
  width: 50px;
  z-index: 10;
  right: 60px;
  left: auto;
  top: 0;
  transform: translateY(50%);
  color: rgb(70, 180, 70);
  border-radius: 8px;
}
.swiper-button-next {
  right: 0;
}
.swiper-button-prev::after,
.swiper-button-next::after {
  font-size: 2rem;
}

/* All Media Quiery */
@media screen and (max-width: 1024px) {
  .container {
    width: var(--md-container);
  }
  section {
    margin-top: 6rem;
  }
  header {
    height: 88vh;
  }
  .about-container {
    grid-template-columns: 1fr;
    gap: 0;
  }
  .about-me {
    width: 50%;
    margin: 2rem auto 4rem;
  }
  .about-content p {
    margin: 1rem 0 1.5rem;
  }
  .experience-container {
    grid-template-columns: 1fr;
  }
  .experience-container > div {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
  }
  .experience-content {
    padding: 1rem;
  }
  .portfolio-container {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.2rem;
  }
  .contact-container {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
}
@media screen and (max-width: 600px) {
  .container {
    width: var(--ms-container);
  }
  section > h2 {
    margin-top: 2rem;
  }
  header {
    height: 100vh;
  }
  .social-media, .scroll-down {
    display: none;
  }
  .about-me {
    width: 65%;
    margin: 0 auto 3rem;
  }
  .about-header {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }
  .about-content {
    text-align: center;
  }
  .about-content p {
    margin: 1.5rem 0;
  }
  .experience-container {
    gap: 1rem;
  }
  .experience-container > div {
    width: 100%;
    padding: 2rem 1rem;
  }
  .portfolio-container {
    flex-direction: column;
      max-width: 400px;
      margin: 0 auto;
      margin-top: 7rem;
      gap: 5rem;
  }
  .portfolio-img {
    width: 100%;
  }
  .contact-container {
    width: var(--ms-container);
  }
  footer ul {
    flex-direction: column;
    gap: 1.5rem;
  }
  .footer-media {
    margin-bottom: 1rem;
  }
}